.our-team {
  padding: 115px 0;
  height: 620px;
  width: 1170px;
  // display: grid;
  // grid-template-columns: 25% 25% 25% 25%;
  display: flex;
  justify-content: space-around;

.person__card {
  width: 23%;
  height: 100%;

  .avatar1{
    background-image: url('../image/avatar1.png');
    width: 100%;
    height: 23%;
    background-size: cover;
  background-repeat: no-repeat;
  }

    .person-card__name {
      font-family: 'Raleway', sans-serif;
      font-size: 24px;
      font-weight: 700;
      margin-top: 30px;
      color: #2D3033;
    }

    .person-card__post {
      font-family: 'Raleway', sans-serif;
      font-weight: 400;
      font-size: 18px;
      color: #FF4343;
      text-transform: uppercase;
    }

    .person-card__desc {
      color: #2D3033;
      margin: 17px 0 37px 0;
      font-family: 'Raleway', sans-serif;
      font-weight: 400;
      font-size: 18px;
    }

    a {
      text-decoration: none;
    }
  }
}