@media (max-width: 1024px) {
  .container {
    min-width: 100%;
    padding: 0 15px;
  }

  .background-banner {
    max-height: 770px;
  }

  .banner {
    h1 {
      font-size: 48px;
    }

    .banner__text {
      padding: 0 100px;
    }
  }

  .features {
    min-height: 690px;
    padding: 80px 15px;

    .features__card {
      img {
        width: 85px;
        height: 85px;
      }

      .heading {
        font-size: 18px;
        margin-top: 20px;
      }

      .desc {
        font-size: 14px;
      }
    }
  }
  .work #background .block__text 
  {.img__head{
    font-size: 18px;
    padding-top: 80px;
  }
  .img__desc{
    font-size: 14px;
  }
}
.our-team.container {
  padding: 80px 15px;
  max-width: 984px;
  .person__card:not(.person__card:last-child){
    padding-right: 15px;
  }
  .person__card{
      &__avatar img {
        width: 227px;
        height: 227px;
      }
}
}
}