.download {
  height: 500px;
  padding: 150px 0 0 0;
  text-align: center;

  .download__title {
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    font-size: 30px;
    height: 70px;
  }

  .download__text {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 18px;
    margin-bottom: 50px;
    text-transform: uppercase;
  }

  .download__btn {
    background-color: #FF4343;
    color: #fff;
    height: 80px;
    width: 370px;
    font-family: 'Raleway', sans-serif;
    font-weight: 900;
    font-size: 24px;
    border: none;
    text-transform: uppercase;

    &:hover {
      background-color: #E71414;
    }

    &:active {
      box-shadow: 4px 4px 30px 0px #FF4343;
      background-color: #FF4343;
    }
  }
}